<template>
  <div class="viewbox">

    <el-tabs :tab-position="tabPosition"
             v-model="activeName">
      <el-tab-pane label="秘书处"
                   name="first" id="first">
        <el-card class="box-card">
          <div slot="header"
               class="clearfix">
            <span>秘书处资料卡</span>
            <el-button style="float: right; padding: 3px 0"
                       type="text">操作按钮</el-button>
          </div>
          <div v-for="o in 4"
               :key="o"
               class="text item">
            {{'列表内容 ' + o }}
          </div>
        </el-card>
      </el-tab-pane>
      <el-tab-pane label="竞赛部"
                   name="second" id="second">配置管理</el-tab-pane>
      <el-tab-pane label="宣传部"
                   name="third">角色管理</el-tab-pane>
      <el-tab-pane label="组织部"
                   name="fouth">定时任务补偿</el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
let link = location.hash;
let nav = link.split('#')[2];
if (!nav) nav = 'tab-first';
export default {
  data () {
    return {
      tabPosition: 'left',
      activeName: nav
    };
  },
  methods: {
    handleBtnClick () {
      this.activeName = nav;
    }
  }
};
</script>

<style>
.el-main {
  height: auto;
}
.viewbox {
  margin-top: 5px;
  width: 75%;
  display: block;
  height: auto;
}
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: '';
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 480px;
}
</style>



